$font-stack: 'Outfit', sans-serif;

//Colors
$white: hsl(0, 0%, 100%);
$lightGray: hsl(212, 45%, 89%);
$grayishBlue: hsl(220, 15%, 55%);
$darkBlue: hsl(218, 44%, 22%);

#qr-code {
    background-color: $white;
    width: 320px;
    margin: 152px auto;
    border-radius: 20px;

    img {
        width: 288px;
        height: 288px;
        margin: 16px 16px 0 16px;
        border-radius: 10px;
        filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    }

    h1 {
        color: $darkBlue;
        margin: 24px 16px 16px 16px;
        font-family: $font-stack;
        font-style: normal;
        font-weight: 700;
        font-size: 22px;
        line-height: 28px;
        text-align: center;
    }

    h2 {
        padding: 0 32px 32px 32px;
        margin: 0;
        color: $grayishBlue;
        font-family: $font-stack;
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 19px;
        text-align: center;
    }
}
